/* 기본 스타일 */
body {
  font-family: Arial, Helvetica, sans-serif, serif;
  
  background-color: #f0f0f0;
  font-weight: 600;

  
}
tr{
  border: rgb(175, 175, 175) ;
}
.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.7s;
}

.main-bg {
  height: 500px;
  background-image: url('./image/bg.png');
  background-size: cover;
  background-position: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  
  


 
  
  
}
#container {
  padding-top: 20px;

}
.nav-link {
  margin-left: 10px;
}
.Brand {
  cursor: pointer;
  
  
}
.App {
  text-align: center;
  letter-spacing: -0.8px;
  height: 100%;
  

  
}


#me {
  max-width: 1400px;
  min-width: 1200px;
  margin: 0 auto;
}
.introduce{
  float: left;
  width: calc(100%/3);
  margin-bottom: 50px;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.col-md-4{
 
  /*다크 모드 때 변화*/

  margin: 0px;
}
thead {
  border-bottom: black 2px solid;
}
#Table-title{

  padding-top: 100px;
  padding-bottom: 30px;
}
#Table-site {
  margin: 0 auto;
  width: 60%;
  padding: 10px;
  
  
  
  border: 1px ridge black ;
  border-radius: 0.5rem;
}
.table {
  
  --bs-body-bg: rgb(153,153,153);
  margin-bottom: 0px;
  
  
  border-radius: 0.5rem;
  
  
  

}
.me-auto{
  
  padding-left: 50%;
}
.bar-left{
  float: right;
}
.night-mode{
  height: 50px;
  width: 50px;
  background-color: 
  rgb(207, 215, 223);
  position: relative;
  border-radius: 50%;
  left: 90%;
  top: 90%;
  position: absolute;
  position: fixed;
  cursor: pointer;
  
  
  
  
}

 #moon{
  padding-right: 8px;
  margin: 0 auto;
  position: absolute;
  top: 20%;
  left: 15%;
} 
#image{
  padding-top: 10px;
}
.bar{
  position: fixed;
  top: 0; left: 0;
  width: 100%;
  background-color: #f0f0f0;
  border-bottom: #868686 1px inset;
  z-index: 10;
}
.img {
  cursor: pointer;
}
.row{
  padding-top: 50px;
}
.col-md-6.mt-4{
  border: 1px ridge black ;
  border-radius: 0.5rem;
  width: 300px;
  margin-left: 40px;
}
.btn.btn-danger{
  margin-top: 30%;
  margin-bottom: 10%;
}
#td {
  padding-top: 20px;
}
.carousel-caption{
  padding-bottom: 60px;
}
.carousel-control-prev {
  padding-top: 70px;
}
.carousel-control-next {
  padding-top: 70px;
}
#welcome {
  margin-left: 30%;
  margin-right: 20%;
  width: 40%;
  
  
}
#td1 {
  font-size: 20px;
  margin-top: 15px;
  background-color: rgb(153,153,153);
   border: rgb(153,153,153)
}
.footer-subtitle {
  font-size: 14px;

  margin: 18px 0 11px 0;
  color: black;
}
.P {
  margin-bottom: 9px;
  color: #999999;
  font-size: 12px
}
.fast {
  border: 1px solid #bdbcbc;
  border-radius: 8px;
  padding: 8px 15px;
  width: 280px;
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
p {
  color: #999999;
  font-size: 12px;
}
.buy{
  margin-left: 1px;
  
  width: 99.8%;
  
  
  
  background-color: rgb(153,153,153);
}
.buy1{
  color: black;
  width: 100px;
  height: 60px;
  margin-bottom: 0px;
}
.로그인창 {

  margin: 0 auto;

  
  width: 40%;
  height: 500px;
  z-index: 9;
  border: #868686 1px ridge;
  border-radius: 2rem;
  
}

#LoginTitle{
  padding-left: 10%;
  font-size: 30px;
  
  padding-top: 5%;
  padding-bottom: 4%;
  font-weight: 700;
  
  float: left;
  width: 20%;
}
#loginButton{
  margin-top: 4%;
  width: 75%;
  float: left; 
  margin-left: 10%;
  height: 70px 
}

#userId:hover span {display:none}
#userId:hover:before {content:"변경"}


